const Icon = props => {
    let El = Logo;
    switch (props.name) {
        case "wallet":
            El = Wallet;
            break;
        case "facebook":
            El = Facebook;
            break;
        case "twitter":
            El = Twitter;
            break;
        case "instagram":
            El = Instagram;
            break;
        case "github":
            El = Github;
            break;
        case "discord":
            El = Discord;
            break;
        default:
            El = Logo;
            break;
    }
    return (
        <El
            className={`rvt-icon rat-icon-${props.name} ${props.className}`.trim()}
        />
    );
};

Icon.propTypes = {
    name: PropTypes.string
};

export default Icon;